.header {
  display: flex;
  flex-direction: column;
  position: relative;
}

.brand {
  background-color: rgba(0, 0, 0, 0.075);
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid #4738A7;
}
.brand img {
  transition: 0.5s width ease-in-out;
  width: 25rem;
  margin: 1rem 0;
}
.brand.fix {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 2147483646;
  background-color: #FFFFFF;
}
.brand.fix img {
  width: 15rem;
}

.nav {
  z-index: 1;
  display: flex;
  justify-content: space-between;
  padding: 1rem 3rem;
}
.nav.fix {
  position: fixed;
  top: 5%;
  left: 0;
  width: 100%;
  z-index: 2147483646;
  background-color: #FFFFFF;
  box-shadow: 3px 4px 2px rgba(0, 0, 0, 0.473);
}
@media (min-width: 1024px) {
  .nav.fix {
    top: 4.5%;
  }
}
.nav .logo2 img {
  width: 7rem;
}
.nav .logo2 img:hover {
  cursor: pointer;
}
.nav .cart img {
  width: 4rem;
}
.nav .cart img:hover {
  cursor: pointer;
}
.nav .container-enlaces {
  display: flex;
  flex-direction: column;
}
.nav .container-enlaces .enlaces {
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin-right: 3rem;
}
.nav .container-enlaces .enlaces.open-menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media (min-width: 1024px) {
  .nav .container-enlaces .enlaces.open-menu {
    flex-direction: row;
  }
}
.nav .container-enlaces .enlaces.open-menu a {
  display: block;
}
.nav .container-enlaces .enlaces .menu-ham {
  filter: invert(100%);
  display: block;
  width: 4rem;
}
.nav .container-enlaces .enlaces .menu-ham:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .nav .container-enlaces .enlaces .menu-ham {
    display: none;
  }
}
.nav .container-enlaces .enlaces a {
  display: none;
  color: rgba(0, 0, 0, 0.753);
  font-weight: 700;
  padding: 1rem;
  border-radius: 1rem;
  transition: 0.5s all;
}
@media (min-width: 768px) {
  .nav .container-enlaces .enlaces a {
    display: flex;
  }
}
.nav .container-enlaces .enlaces a:hover {
  color: #FFFFFF;
  background-color: #90b3cf;
}

.slider {
  width: 100%;
  z-index: 1;
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.075);
  overflow: hidden;
}
.slider .sliderText {
  position: relative;
  display: flex;
  width: 100%;
  height: 4rem;
}
.slider .sliderText h1 {
  margin: auto;
  color: rgba(0, 0, 0, 0.877);
  font-size: 2rem;
  width: 100%;
}
@media (min-width: 1024px) {
  .slider .sliderText h1 {
    width: 99%;
  }
}

.title1,
.title2,
.title3 {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20%;
  top: 25%;
  right: 100%;
  font-size: 5rem;
  animation-duration: 20s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
}

.title1 {
  animation-name: anim-1;
}

.title2 {
  animation-name: anim-2;
}

.title3 {
  animation-name: anim-3;
}

@keyframes anim-1 {
  0%, 8.3% {
    left: -100%;
    opacity: 0;
  }
  8.3%, 25% {
    left: 0%;
    @media (min-width: 1024px) {
      left: 40%;
    }
    opacity: 1;
  }
  33.33%, 100% {
    left: 110%;
    opacity: 0;
  }
}
@keyframes anim-2 {
  0%, 33.33% {
    left: -100%;
    opacity: 0;
  }
  41.63%, 58.29% {
    left: 0%;
    @media (min-width: 1024px) {
      left: 40%;
    }
    opacity: 1;
  }
  66.66%, 100% {
    left: 110%;
    opacity: 0;
  }
}
@keyframes anim-3 {
  0%, 66.66% {
    left: -100%;
    opacity: 0;
  }
  74.96%, 91.62% {
    left: 0%;
    @media (min-width: 1024px) {
      left: 40%;
    }
    opacity: 1;
  }
  100% {
    left: 110%;
    opacity: 0;
  }
}
.slide-down {
  height: 0rem;
  z-index: 1000;
  top: 60%;
  background-color: #FFFFFF;
  position: absolute;
  transition: height 0.2s ease;
  border-bottom: 1px solid #4738A7;
  margin-top: 1.742rem;
}

.shoes-menu {
  z-index: 1000;
  margin-top: 0.5rem;
  transition: height 0.2s ease-in;
  width: 0rem;
  height: 0rem;
  visibility: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}
.shoes-menu a {
  color: rgba(0, 0, 0, 0.753);
}
.shoes-menu p {
  color: rgba(0, 0, 0, 0.753);
  opacity: 0;
  margin: 0.5rem;
  transition: opacity 0.7s ease-in, border 0.2s ease-in-out;
  border-bottom: 2px solid transparent;
}
.shoes-menu p:hover {
  border-bottom: 2px solid #4738A7;
  color: rgba(0, 0, 0, 0.877);
  cursor: pointer;
}

.brand-menu {
  z-index: 1000;
  width: 0rem;
  height: 0rem;
  visibility: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5rem;
}
.brand-menu h2 {
  color: rgba(0, 0, 0, 0.877);
}
.brand-menu img {
  width: 10rem;
}

.location-menu {
  z-index: 1000;
  width: 0rem;
  height: 0rem;
  visibility: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}
.location-menu span {
  color: #90b3cf;
  border-bottom: 2px solid #90b3cf;
}
.location-menu h2 {
  color: rgba(0, 0, 0, 0.877);
}

.opac {
  transition: opacity 0.7s ease-in;
  opacity: 0;
}

.logo-link:hover {
  cursor: pointer;
}

.sign {
  background-image: linear-gradient(to right, #4738a7, #3f5cba, #4a7bc5, #6898cb, #90b3cf);
  margin-top: 5rem;
  width: 100%;
  height: 50vh;
  gap: 0rem;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
}
@media (min-width: 1024px) {
  .sign {
    padding: 1rem 10rem;
    gap: 20rem;
  }
}

.sign-img {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  visibility: hidden;
  width: 0rem;
  height: 0rem;
}
@media (min-width: 768px) {
  .sign-img {
    min-width: 30%;
    width: 50rem;
    height: 50rem;
    visibility: visible;
  }
}
@media (min-width: 1024px) {
  .sign-img {
    min-width: auto;
  }
}
.sign-img img {
  width: 0rem;
  height: 0rem;
}
@media (min-width: 768px) {
  .sign-img img {
    position: absolute;
    backface-visibility: hidden;
    filter: drop-shadow(0px 0px 5px #4738A7);
    animation: bounce infinite 7s;
    width: 80rem;
    visibility: visible;
    height: auto;
  }
}
@media (min-width: 1024px) {
  .sign-img img {
    width: 60rem;
  }
}

@keyframes bounce {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-5rem);
  }
  100% {
    transform: translateY(0);
  }
}
.sing-desc {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  min-width: 70%;
}
@media (min-width: 1024px) {
  .sing-desc {
    min-width: auto;
  }
}
.sing-desc p {
  color: #FFFFFF;
  font-weight: 300;
  font-size: 2rem;
}
@media (min-width: 768px) {
  .sing-desc p {
    font-size: 3rem;
  }
}
.sing-desc h1 {
  margin-bottom: 1rem;
  color: #FFFFFF;
  font-size: 4rem;
}
@media (min-width: 768px) {
  .sing-desc h1 {
    font-size: 6rem;
  }
}
@media (min-width: 1024px) {
  .sing-desc h1 {
    font-size: 10rem;
  }
}
.sing-desc h1 img {
  background-color: white;
  padding: 0.5rem;
  border-radius: 1rem;
}

.divCart {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  transform: translateX(100%);
  transition: transform 0.3s;
  background-color: #FFFFFF;
  z-index: 9999;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.527);
  z-index: 2147483647;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .divCart {
    max-width: 50rem;
  }
}
.divCart .cart-helper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}
.divCart .cart-helper.no-center {
  display: block;
}
.divCart .item {
  visibility: hidden;
}
.divCart .cart-title {
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 1rem;
  border-bottom: 1px solid rgba(0, 0, 0, 0.185);
