}
.divCart .cart-title h1 {
  font-size: 2.5rem;
  font-weight: 700;
}
.divCart .cart-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 33rem;
}
.divCart .cart-content img {
  width: 10rem;
}
.divCart .grey-line {
  background-color: rgba(0, 0, 0, 0.075);
  height: 5%;
}

.divCart.abierto {
  transform: translateX(0%);
}

.showOverlay {
  background-color: rgba(0, 0, 0, 0.63);
  position: fixed;
  z-index: 9999;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
}

.cartEmpty {
  visibility: hidden;
  width: 0rem;
  height: 0rem;
}

.divCart .cart-content .cart-product {
  display: flex;
  align-items: center;
  gap: 2rem;
  width: 90%;
  height: 10.5rem;
  margin: 1rem auto;
}
@media (min-width: 768px) {
  .divCart .cart-content .cart-product {
    height: 80%;
    margin: 0.7rem auto;
  }
}
.divCart .cart-content .cart-product img {
  width: 12rem;
  height: 12rem;
}
.divCart .cart-content .cart-product h2 {
  font-size: 2rem;
  margin: 0;
}
.divCart .cart-content .cart-product p {
  font-size: 1.4rem;
  font-weight: 900;
}
.divCart .cart-content .cart-product .cart-words {
  width: 65%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 2rem;
}
.divCart .cart-content .cart-product .name-trash-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 5rem;
}
.divCart .cart-content .cart-product .name-trash-container img {
  width: 3rem;
  height: 3rem;
}
.divCart .cart-content .cart-product .name-trash-container img:hover {
  cursor: pointer;
}

.dissapear {
  visibility: hidden;
  display: none;
  height: 0;
  width: 0;
  border: none;
}

.divCart .cart-content .price-container {
  display: flex;
  justify-content: space-between;
  gap: 5rem;
