}
.cart-purchase-container .sub-total,
.cart-purchase-container .total-price {
  width: 90%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.buy-button {
  padding: 1rem;
  display: block;
  width: 50%;
  border: none;
  background-color: #111111;
  color: #FFFFFF;
  border-radius: 1rem;
  transition: background-color 0.3s ease;
}
.buy-button:hover {
  cursor: pointer;
  background-color: #1e1e1e;
}

body .max-cart {
  position: fixed;
  bottom: 2%;
  left: 2%;
  background-color: rgb(245, 55, 55);
  height: 4rem;
  width: 4rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}
body .max-cart p {
  color: #FFFFFF;
}
body .max-cart:hover {
  cursor: pointer;
}
body .max-cart:hover::before {
  content: "";
  position: absolute;
  top: -2rem;
  left: 50%;
  transform: translateX(-50%);
  border-left: 0.75rem solid transparent;
  border-right: 0.75rem solid transparent;
  border-top: 1.5rem solid red;
}
body .max-cart:hover::after {
  content: "Max 4 products";
  position: absolute;
  top: -6.5rem;
  left: 50%;
  transform: translateX(-50%);
  color: white;
  background-color: red;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  font-size: 1.6rem;
  width: 10rem;
}

.product-price1,
.product-price2,
.product-price3,
.product-price4 {
  width: 40%;
