
// Mixins //
@mixin telefono{
    @media (min-width: $telefono){
        @content
    }
}

@mixin tablet{
    @media (min-width: $tablet){
        @content
    }
}

@mixin desktop{
    @media (min-width: $desktop) {
        @content
    }
}
@mixin desktopXL{
    @media (min-width: $desktopXL) {
        @content
    }
}
@mixin grid($columnas, $gap){
    display:grid;
    grid-template-columns: repeat($columnas, 1fr);
    gap: $gap;
}
